.navbar{
    display: flex;;
    justify-content: 16px;
    box-shadow: 0 1px 3px -2px black;
}

.nav-logo{
    display: flex;
    align-items: center;
    gap:10px;

}

.nav-logo p{
    color:black;
    font-size:38px;
    font-weight:600;

}

.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: green;
    font-size: 20px;
    font-weight:500;
}

.nav-menu li{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap:3px;
    cursor: pointer;
}

.nav-menu hr {
    border: none;
    width:80%;
    height:3px;
    border-radius:10px ;
    color:blueviolet;
}

.nav-login-cart{
    display: flex;
    align-items: center;
    gap:45px;
}

.nav-login-cart button{
    width:157px;
    height:58px;
    outline:none;
    border:1px solid gray;
    color: violet;
    border-radius: 75px;
    font-size:20px;
    font-weight:500;
    background: white;
    cursor:pointer;
}
.nav-login-cart button:active{
    background: #f3f3f3;
}

.navbar-cart-count{
    width:22px;
    height:22px;
    display:flex;
    justify-content: center;
    align-items:center;
    margin-top: -35px;
    margin-left:-55px;
    border-radius:11px;
    font-size: 14px;
    background-color: red;
    color:white;

}

img{
    height:4em;
}

button{
    align-items: right;
    margin-left: 2em;
}